.App {
  /* can be improved to become fully responsive by @media */
  min-width: 320px;
  padding: 0.9rem;

  display: flex;
  flex-direction: column;
}

.App h1 {
  color: black;
  font-size: 1.3rem;
  font-weight: 500;

  margin-left: 0.2rem;
  margin-bottom: 1.5rem;
}

.app-body-flex-container {
  align-self: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: min(650px, 100%);
}
